html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: none;
}

ins {
  text-decoration: none;
}

del {
  text-decoration: line-through;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.wrapper {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}

img {
  width: 100%;
  height: auto;
}

@font-face {
  font-family: 'Aleo';
  src: url("../fonts/AleoRegular/aleo-regular-webfont.eot");
  src: local("Aleo Regular"), local("Aleo-Regular"), url("../fonts/AleoRegular/aleo-regular-webfont.eot?#iefix") format("embedded-opentype"), url("../fonts/AleoRegular/aleo-regular-webfont.woff") format("woff"), url("../fonts/AleoRegular/aleo-regular-webfont.woff2") format("woff2"), url("../fonts/AleoRegular/aleo-regular-webfont.svg") format("svg"), url("../fonts/AleoRegular/aleo-regular-webfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

.btn {
  width: 225px;
  height: 60px;
  background-color: #5474fd;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  color: #fff;
  margin-top: 48px;
  border: none;
  cursor: pointer;
  transition: all 0.3s linear;
}

.btn:hover {
  color: #5474fd;
  background-color: #fff;
}

.mainScreen {
  background: url("../img/mainScreen/Image.png") no-repeat;
  background-size: cover;
}

.mainText {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100vh;
  width: 100%;
  color: #fff;
  font-family: "Josefin Sans", sans-serif;
}

.mainText h1 {
  font-size: 48px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  letter-spacing: 5px;
  padding-bottom: 15px;
}

.mainText__desc {
  font-size: 22px;
  font-weight: 400;
  line-height: 26px;
  letter-spacing: 6.82px;
  padding-bottom: 30px;
}

.mainText__form {
  max-width: 650px;
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 120px;
}

.mainText__form form {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}

.mainText__form form input[type="text"] {
  max-width: 500px;
  width: 100%;
  min-width: 300px;
  color: #999999;
  font-size: 14px;
  font-weight: 400;
  line-height: 26px;
  letter-spacing: 2.8px;
  text-align: center;
  border: none;
  padding: 15px 0;
  margin-top: 20px;
}

.mainText__form--search {
  position: relative;
  cursor: pointer;
  height: 57px;
  margin-top: 20px;
}

.mainText__form--search input[type="submit"] {
  width: 150px;
  height: 100%;
  border: 2px solid #ffffff;
  background-color: #5474fd;
  font-family: Aleo, sans-serif;
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  line-height: 26px;
  text-transform: uppercase;
  letter-spacing: 1.4px;
  cursor: pointer;
  transition: all 0.3s linear;
}

.mainText__form--search input[type="submit"]:hover {
  background-color: #224bfc;
}

.mainText__form--search img {
  position: absolute;
  width: 20px;
  height: 20px;
  right: 15px;
  top: 50%;
  transform: translateY(-10px);
}

.mainText__scroll {
  cursor: pointer;
  position: relative;
}

.mainText__scroll a {
  font-size: 14px;
  font-weight: 400;
  line-height: 26px;
  letter-spacing: 2.8px;
  text-decoration: none;
  color: #fff;
}

.mainText__scroll:after {
  position: absolute;
  content: '';
  width: 8px;
  height: 8px;
  border-left: 1px solid white;
  border-bottom: 1px solid white;
  transform: rotate(-45deg);
  left: 45px;
  top: 30px;
}

.navigations {
  background-color: #5474fd;
}

.navigationsWrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.navigationsWrapper__logo {
  color: #fff;
  font-family: Josefin Sans, sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  letter-spacing: 2.4px;
  padding: 30px 0;
}

.navigationsWrapper__nav ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.navigationsWrapper__nav ul li a {
  text-decoration: none;
  color: #ffffff;
  font-family: Aleo, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 26px;
  text-transform: uppercase;
  transition: all 0.3s linear;
}

.navigationsWrapper__nav ul li a:hover {
  text-decoration: underline;
}

.navigationsWrapper__nav ul li:not(:first-child) {
  padding-left: 42px;
}

.chooseUs {
  background: url("../img/chooseUs/bg_whyChooseUs.png") no-repeat center center;
  background-size: cover;
}

.chooseUsWrapper {
  display: flex;
  flex-wrap: wrap;
  padding: 120px 0 55px;
  justify-content: space-between;
  color: #fff;
  font-family: Aleo, sans-serif;
  align-items: center;
}

.chooseUsWrapper__text {
  flex-basis: 530px;
}

.chooseUsWrapper__text h2 {
  font-size: 36px;
  font-weight: 400;
  line-height: 26px;
  padding-bottom: 62px;
}

.chooseUsWrapper__text p {
  color: #e8e8e8;
  font-family: Josefin Sans, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  letter-spacing: 1.6px;
}

.chooseUsWrapper__imgs {
  flex-basis: 475px;
  display: flex;
  flex-direction: column;
  padding: 30px 0;
}

.chooseUsWrapper__imgs--first {
  align-self: flex-start;
  margin: 5px;
}

.chooseUsWrapper__imgs--second {
  align-self: flex-end;
  margin: 5px;
}

.process {
  background-color: #333333;
}

.processWrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 100px 0 119px;
  background: url("../img/process/Shape.svg") no-repeat center 60px;
}

.processWrapper__item {
  display: flex;
  flex-wrap: wrap;
  padding: 10px 20px;
  flex-direction: column;
  align-items: center;
}

.processWrapper__item--img {
  background-color: #333;
  padding: 30px;
  border-radius: 50%;
}

.processWrapper__item--img img {
  width: 60px;
  height: 60px;
  transition: all 0.3s linear;
  cursor: pointer;
}

.processWrapper__item--img img:hover {
  transform: scale(1.3);
}

.processWrapper__item h2 {
  color: #e8e8e8;
  font-family: Aleo, sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 26px;
  padding-top: 21px;
}

.spainFirs {
  background: url("../img/spain/bg_first.png") no-repeat center center;
  background-size: cover;
}

.spainSecond {
  background: url("../img/spain/bg_second.png") no-repeat center center;
  background-size: cover;
}

.spainWrapper {
  display: flex;
  justify-content: flex-start;
  padding: 229px 0 100px;
}

.spainWrapper__block {
  max-width: 540px;
  background-color: #232323;
  opacity: 0.9;
  font-family: Josefin Sans, sans-serif;
  padding: 50px 30px;
}

.spainWrapper__block span {
  color: #e8e8e8;
  font-size: 20px;
  font-weight: 400;
  line-height: 26px;
  letter-spacing: 1.5px;
  padding-bottom: 30px;
  display: inline-block;
}

.spainWrapper__block h2 {
  color: #ffffff;
  font-size: 20px;
  font-weight: 400;
  line-height: 26px;
  text-transform: uppercase;
  padding-bottom: 29px;
}

.spainWrapper__block p {
  color: #e8e8e8;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  padding-bottom: 48px;
}

.spainSecondWrapper {
  justify-content: flex-end;
}

.touristSay {
  background: url("../img/touristSay/bg_touristSay.png") no-repeat center center;
  background-size: cover;
}

.touristSayWrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 120px 0 200px;
}

.touristSayWrapper h2 {
  color: #fff;
  font-family: Aleo, sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 26px;
  padding-bottom: 74px;
  text-align: center;
}

.touristSayWrapper__story {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.touristSayWrapper__story--img {
  margin-top: 20px;
  flex-basis: 445px;
}

.touristSayWrapper__story--img img {
  height: 100%;
}

.touristSayWrapper__story--text {
  margin-top: 20px;
  flex-basis: 665px;
  background-color: #333;
  padding: 50px 60px;
  font-family: Josefin Sans, sans-serif;
}

.touristSayWrapper__story--text h3 {
  color: #fefefe;
  font-size: 20px;
  font-weight: 400;
  line-height: 26px;
  text-transform: uppercase;
  padding-bottom: 33px;
}

.touristSayWrapper__story--text p {
  color: #e8e8e8;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  position: relative;
}

.touristSayWrapper__story--text p i {
  font-size: 25px;
  color: #5474fd;
  position: relative;
  top: -15px;
  padding-right: 15px;
}

.footer {
  background-color: #232323;
}

.footerWrapper {
  color: #fff;
  font-family: Josefin Sans, sans-serif;
  text-align: center;
  padding: 120px 0 53px;
}

.footerWrapper h3 {
  font-size: 24px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  letter-spacing: 3.36px;
  padding-bottom: 15px;
}

.footerWrapper span {
  display: inline-block;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  letter-spacing: 1.6px;
  padding-bottom: 30px;
}

.footerWrapper__socials i {
  color: #e8e8e8;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  letter-spacing: 1.6px;
  cursor: pointer;
  transition: all 0.3s linear;
}

.footerWrapper__socials i:not(:first-child) {
  padding-left: 24px;
}

.footerWrapper__socials i:hover {
  color: #5474fd;
  transform: scale(1.2);
}

@media screen and (max-width: 1153px) {
  .chooseUsWrapper {
    justify-content: space-around;
  }

  .chooseUsWrapper__text {
    text-align: center;
  }

  .chooseUsWrapper__imgs {
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: row;
    flex-basis: 100%;
  }

  .processWrapper {
    background: none;
  }
}

@media screen and (max-width: 800px) {
  .navigationsWrapper {
    justify-content: center;
  }

  .navigationsWrapper ul li {
    padding: 20px;
  }

  .processWrapper {
    padding: 20px 0;
  }

  .spainWrapper__block {
    text-align: center;
  }
}

@media screen and (max-width: 360px) {
  .mainText h1 {
    font-size: 30px;
  }
}